import React, { useEffect, useState } from 'react';
import styled from 'styled-components';
import { css } from "@emotion/react";
import InnerContainer from '../../../components/Container/InnerContainer';
import Ticket from '../../../components/Ticket';
import HashLoader from "react-spinners/HashLoader";
import { colors } from '../../../styles/colors';
import LinkedText from '../../../components/LinkedText';

const TicketSection = ({ checkResult }) => {
  const [ isLoading, setIsLoading ] = useState(true)

  useEffect(() => {
    setTimeout(() => {
      setIsLoading(false)
    }, [1500])
  }, [])

  return (
    <InnerContainer>
      <LoadingContainer isLoading={isLoading}>
        <HashLoader loading={isLoading} css={spinnerStyle} size={50} color={colors.PRIMARY}/>
      </LoadingContainer>
      <TicketContainer>
        <Ticket checkResult={checkResult}/>
        <LinkedText to={`/service-reservation/`}>첫화면으로 돌아가기</LinkedText>
      </TicketContainer>
    </InnerContainer>
  );
}

const LoadingContainer = styled.div`
  display: ${props => props.isLoading? 'flex' : 'none'};;
  justify-content: center;
  align-items: center;
  z-index: ${props => props.isLoading? 3 : -1};
  background-color: ${colors.WHITE};
  height: 94vh;
`;

const TicketContainer = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 94vh;
  z-index: 1;

  @media screen and (min-width: 768px) {
    max-width: 450px;
    margin: 0 auto;
  }
`;

const spinnerStyle = css`
  margin: 0 auto;
  z-index: ${props => props.loading? 3 : -1};
`;

export default TicketSection;